import jwt from "jsonwebtoken"

const secret = process.env.JWT_SECRET;

export const verifyJwt = (jwtToken) => {
    return new Promise((resolve, reject) => {
        jwt.verify(jwtToken, secret, function (err, decoded) {
            if (err) {
                reject(err);
            } else {
                resolve(decoded);
            }
        });
    });
}